.footer {

	&__inner {
		margin: 50px 0;

		@media (min-width: 320px) and (max-width: 676px) {
			margin: 25px 0;
		}
	}

	&__nav {
		display: flex;
		justify-content: center;

		@media (min-width: 320px) and (max-width: 676px) {
			flex-direction: column;
		}

		&-link {
			padding-left: 30px;
			font-size: 16px;
			font-weight: 400;
			color: #000;
			transition: 0.3s;

			@media (min-width: 320px) and (max-width: 676px) {
				margin: 0 auto;
				margin-bottom: 10px;
				padding-left: 0;
			}

			&:first-child {
				padding-left: 0;
			}

			&:hover {
				text-decoration: none;
				color: #42b3e5;
			}
		}
	}

	&__copyright {
		padding-top: 20px;
		text-align: center;

		span {
			color: #737373;
		}
	}

	&__social {
		padding-top: 20px;
		display: flex;
		justify-content: center;
	}

	&__item {

		margin-left: 20px;

		&:first-child {
			margin-left: 0;
		}

		&-icon {
			width: 30px;
			height: 30px;
			
			padding: 7px;
			fill: #fff;
			background-color: #3984F3;
			border-radius: 50%;	
		}
	}
}