.expert {

	margin-bottom: 75px;

	&__inner {

	}

	&__header {
		width: 75%;
		margin: 0 auto;
		text-align: center;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
		}

		&-title {
			color: #091E40;
			font-size: 24px;
			font-family: 'Nunito Sans SemiBold', sans-serif;
		}

		&-line {
			margin: 0 auto;
			margin-top: 25px;
			width: 7%;
			height: 4px;
			background-color: #3984F3;

			@media (min-width: 320px) and (max-width: 991px) {
				width: 15%;
			}
		}

		&-subtitle {
			padding-top: 25px;

			color: #979AA1;
		}
	}

	&__slider {
		display: flex;
		justify-content: center;
	}

	&__item {
		text-align: center;
		margin: 20px 0;

		&-img {
			margin: 0 auto;
			width: 50%;

			img {
				display: block;
				width: 100%;
				height: auto;
				border-radius: 50%;
			}
		}

		&-name {
			margin-top: 15px;
			color: #091E40;
			font-family: 'Nunito Sans Bold', sans-serif;
		}

		&-specialty {
			margin-top: 5px;
			color: #979AA1;
		}
	}
}