.places {

	&__inner {
		margin: 100px 0;
		display: flex;

		@media (min-width: 320px) and (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__photo {
		width: 60%;
		display: flex;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
			order: 2;
		}

		&-img {
			position: relative;
			padding-left: 30px;

			@media (min-width: 320px) and (max-width: 480px) {
				padding: 0 15px;
				margin-bottom: 15px;

				&:first-child {
					padding-left: 15px;
				}
			}

			&:first-child {
				padding-left: 0;

				@media (min-width: 320px) and (max-width: 480px) {
					padding-left: 15px;
				}
			}
			
			img {
				display: block;
				width: 100%;
				height: auto;
			}
		}

		&-price {
			position: absolute;
			top: 0;
			margin-top: 20px;
			padding: 3px 15px;
			color: #fff;
			background-color: #3984F3;

			@media (min-width: 320px) and (max-width: 567px) {
				font-size: 12px;
			}
		}

		&-title {
			position: absolute;
			padding-left: 10px;
			margin-top: -25px;
			bottom: 1;
			color: #fff;

			@media (min-width: 320px) and (max-width: 567px) {
				margin-top: -35px;
				font-size: 12px;
			}
		}
	}

	&__header {
		width: 40%;
		padding-left: 100px;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
			padding-left: 0;
			margin-bottom: 30px;
			text-align: center;
			order: 1;
		}

		&-title {
			color: #091E40;
			font-size: 24px;
			font-family: 'Nunito Sans SemiBold', sans-serif;
		}

		&-line {
			margin-top: 20px;
			width: 15%;
			height: 4px;
			background-color: #3984F3;

			@media (min-width: 320px) and (max-width: 991px) {
				margin: 20px auto 0;
			}
		}

		&-subtitle {
			padding-top: 25px;

			color: #979AA1;
		}

		&-button {
			margin-top: 25px;
			padding: 10px 25px;

			color: #fff;
			font-family: 'Nunito Sans SemiBold', sans-serif;
			background-color: #3984F3;
		}
	}
}