.intro {

	&__inner {
		background: url(../img/bg.jpg) center no-repeat;
		background-color: #B2C7DF;
		background-size: cover;
		height: 1000px;

		@media (min-width: 320px) and (max-width: 991px) {
			background: url(../img/bg-mobil.jpg) center no-repeat;
			background-color: #B2C7DF;
			background-size: cover;
			height: auto;
			padding: 50px 0;
		}
	}

	&__header {
		width: 50%;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
			text-align: center;
		}

		&-title {
			padding-top: 250px;

			color: #091E40;
			font-size: 45px;
			font-family: 'Nunito Sans ExtraBold', sans-serif;

			@media (min-width: 320px) and (max-width: 991px) {
				font-size: 36px;
				padding-top: 0;
			}
		}

		&-subtitle {
			width: 50%;
			padding-top: 50px;

			color: #979AA1;

			@media (min-width: 320px) and (max-width: 991px) {
				width: 100%;
				padding-top: 20px;
			}
		}

		&-button {
			margin-top: 50px;
			padding: 10px 25px;

			color: #fff;
			font-family: 'Nunito Sans SemiBold', sans-serif;
			background-color: #3984F3;

			@media (min-width: 320px) and (max-width: 991px) {
				margin-top: 20px;
			}
		}
	}
}