.categories {

	&__inner {
		margin: 100px 0;
	}

	&__header {
		width: 75%;
		margin: 0 auto;
		text-align: center;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
		}

		&-title {
			color: #091E40;
			font-size: 24px;
			font-family: 'Nunito Sans SemiBold', sans-serif;
		}

		&-line {
			margin: 0 auto;
			margin-top: 25px;
			width: 7%;
			height: 4px;
			background-color: #3984F3;

			@media (min-width: 320px) and (max-width: 991px) {
				width: 15%;
			}
		}

		&-subtitle {
			padding-top: 25px;

			color: #979AA1;
		}
	}

	&__slider {
		display: flex;
		margin-top: 50px;
	}

	&__item {
		padding-left: 15px;

		@media (min-width: 320px) and (max-width: 991px) {
			margin-bottom: 15px;
		}

		&:first-child {
			padding-left: 0;
		}
		
		&-img {
			position: relative;

			img {
				display: block;
				width: 100%;
				height: auto;
			}
		}

		&-title {
			position: absolute;
			padding-left: 20px;
			margin-top: -25px;
			bottom: 1;
			color: #fff;
		}
	}
}