.features {

	&__inner {
		margin: 100px 0;
	}

	&__header {
		width: 75%;
		margin: 0 auto;
		text-align: center;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
		}

		&-title {
			color: #091E40;
			font-size: 24px;
			font-family: 'Nunito Sans SemiBold', sans-serif;
		}

		&-line {
			margin: 0 auto;
			margin-top: 25px;
			width: 7%;
			height: 4px;
			background-color: #3984F3;

			@media (min-width: 320px) and (max-width: 991px) {
				width: 15%;
			}
		}

		&-subtitle {
			padding-top: 25px;

			color: #979AA1;
		}
	}

	&__video {
		display: flex;
		margin-top: 50px;

		@media (min-width: 320px) and (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__play {
		width: 70%;

		@media (min-width: 320px) and (max-width: 991px) {
			width: 100%;
		}

		&-img {
			position: relative;
			
			img {
				display: block;
				width: 100%;
				height: auto;
			}
		}

		&-button {
			position: absolute;
			top: 0;
			width: 50px;
			height: 50px;
			margin-left: calc(50% - 50px);
			margin-top: 25%;
			fill: #fff;
		}

		&-title {
			position: absolute;
			padding-left: 30px;
			margin-top: -70px;
			bottom: 1;
			color: #fff;
			font-size: 20px;
		}

		&-time {
			position: absolute;
			padding-left: 30px;
			margin-top: -40px;
			bottom: 1;
			color: #979AA1;
			font-size: 20px;
		}
	}

	&__slider {
		width: 30%;
		padding-left: 15px;

		@media (min-width: 320px) and (max-width: 991px) {
			display: flex;
			margin: 0 auto;
			padding-left: 0;
			width: 100%;
		}
	}

	&__item {
		display: flex;
		padding: 15px;

		transition: .3s;

		@media (min-width: 320px) and (max-width: 991px) {
			flex-direction: column;
			flex-wrap: wrap;
			margin-top: 25px;
			width: 50%;
		}

		&:hover {
			background-color: #F0F0F0;
			cursor: pointer;
		}

		&-img {

			width: 50%;

			@media (min-width: 320px) and (max-width: 991px) {
				width: 100%;
			}

			img {
				display: block;
				width: 100%;
				height: auto;
			}
		}

		&-wrap {
			display: flex;
			flex-direction: column;
			width: 50%;
			padding-left: 10px;

			@media (min-width: 320px) and (max-width: 991px) {
				width: 100%;
				padding-left: 0;
			}
		}

		&-title {
			color: #091E40;
			font-size: 16px;
			font-family: 'Nunito Sans SemiBold', sans-serif;
		}

		&-time {
			bottom: 1;
			color: #979AA1;
		}
	}
}