.header {

	position: fixed;
	background-color: #fff;
	width: 100%;
	z-index: 9999;
	
	&__fixed {
		height: 100px;
	}

	&__inner {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		height: 100px;
	}
}

.nav {

	@media (min-width: 320px) and (max-width: 767px) {
		display: none;
	}

	&__link {
		color: #737373;
		font-size: 16px;
		font-weight: 400;
		transition: 0.3s;

		padding-left: 50px;

		&-button {
			margin-left: 50px;
			padding: 10px 25px;

			color: #3984F3;
			border: 3px solid #3984F3;
			cursor: pointer;
		}

		&:first-child {
			padding-left: 0;
		}

		&:hover {
			text-decoration: none;
			color: #42b3e5;
		}
	}
}

// Mobile
.mobile {
	display: none;

	@media (min-width: 320px) and (max-width: 767px) {
		display: block;
	}
}

// Мобильное меню
.m-nav {
	display: flex;
	flex-direction: column;
	align-items: center;

	width: 100%;
	height: 100%;
	padding: 40px 0;
	
	font-size: 16px;
	background-color: #fff;

	position: fixed;
	right: -100%;
	top: 0;

	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	transition: all .3s linear;

	z-index: 1000;

	&__link {
		display: flex;
		justify-content: center;
		margin-bottom: 25px;
		color: #000;
		transition: .3s;

		&-button {
			padding: 10px 25px;

			color: #3984F3;
			border: 3px solid #3984F3;
			cursor: pointer;
		}

		&:hover {
			color: #42b3e5;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.m-nav-open {
	transform: translateX(-100%);
}

// Burger
.hamburger {
	position: absolute !important;
	top: 20px !important;
	right: 15px !important;

	padding: 0 !important;
	outline: none !important;

	z-index: 9999 !important;

	@media (min-width: 320px) and (max-width: 767px) {
		bottom: 15px;
	}
}

.hamburger-inner, .hamburger-inner:after, .hamburger-inner:before {
    background-color: #000 !important;
}

.hamburger.is-active:hover, .hamburger:hover {
    opacity: 1 !important;
}

// Slick Custom Prev and Next
.prev-arrow {
	position: absolute;
	left: 40%;
	top: 100%;

	@media (min-width: 320px) and (max-width: 480px) {
		left: 35%;
	}
}

.next-arrow {
	position: absolute;
	right: 40%;
	top: 100%;

	@media (min-width: 320px) and (max-width: 480px) {
		right: 35%;
	}
}

.prev-arrow, .next-arrow {
	width: 40px;
	height: 40px;
	padding: 10px;

	// background-color: #515B61;
	border-radius: 50%;

	z-index: 100;
	cursor: pointer;

	fill: #A0A0A0;
}