.statement {

	&__inner {
		background: url(../img/statement.jpg) center no-repeat;
		background-color: #B2C7DF;
		background-size: cover;
		padding: 200px 0;

		@media (min-width: 320px) and (max-width: 991px) {
			padding: 50px 0;
		}
	}

	&__header {
		text-align: center;

		&-title {
			color: #fff;
			font-size: 30px;
			font-family: 'Nunito Sans SemiBold', sans-serif;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 20px;
			}
		}

		&-subtitle {
			color: #fff;
			font-size: 35px;
			font-family: 'Nunito Sans ExtraBold', sans-serif;

			@media (min-width: 320px) and (max-width: 767px) {
				font-size: 20px;
			}
		}
	}

	&__form {
		margin-top: 50px;
		text-align: center;

		input {
			margin-left: 25px;
			padding: 10px 20px;
			font-family: 'Nunito Sans SemiBold', sans-serif;
			border: none;
			color: #000;

			@media (min-width: 320px) and (max-width: 991px) {
				display: flex;
				flex-direction: column;
				margin: 30px auto;
			}

			&:first-child {
				margin-left: 0;

				@media (min-width: 320px) and (max-width: 991px) {
					margin: 30px auto;
				}
			}
		}		

		button {
			margin-left: 25px;
			padding: 10px 60px;
			color: #fff;
			font-family: 'Nunito Sans SemiBold', sans-serif;
			background-color: #3984F3;
			border: none;
			cursor: pointer;

			@media (min-width: 320px) and (max-width: 991px) {
				padding: 10px 70px;
				display: flex;
				flex-direction: column;
				margin: 15px auto;
			}
		}
	}
}